html body{margin:0;padding:0}*{font-family:Roboto,sans-serif}#root{display:grid;grid-template-columns:1fr;justify-items:center;align-items:center;height:100vh}#root #App{margin:20px;display:grid;grid-template-columns:auto auto;grid-template-rows:1fr 1fr;justify-items:center;border-radius:20px;grid-gap:10px;box-shadow:rgba(0,0,0,.582) 0 0 10px 1px}#root #App .Title{grid-column:1/2;grid-row:1/2;margin:0 20px;display:grid;padding:0}#root #App .Title h1{margin:0;font-size:50px;place-self:center}#root #App .Form{grid-column:1/2;grid-row:2/3;display:grid;grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr;place-items:center;grid-gap:5px;margin:0 10px 10px 10px}#root #App .Form *{border-radius:5px;border:1px solid;padding:5px}#root #App .Form .Location{width:10em}#root #App .Form .Country{width:auto;max-width:100%;text-align:center}#root #App .Form .Country option{word-wrap:break-word}#root #App #Weather{grid-column:2/3;grid-row:1/3;border-radius:0 20px 20px 0;font-size:20px;display:grid}#root #App #Weather .Text{place-self:center}#root #App #Weather .Text p{margin:10px 20px}